<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>发布文章</el-breadcrumb-item>
      </el-breadcrumb> -->
      <div>
        <h3>发布领养信息</h3>
      </div>
    </div>
    <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px">

      <el-row>
        <el-col :span="12">
          <el-form-item class="topMargin" label="标题：" prop="title">
            <el-row>
              <el-col>
                <el-input v-model="form.title"></el-input>
              </el-col>
            </el-row>
          </el-form-item>
          
          <el-form-item class="topMargin" label="宠物类型：" prop="petType">
            <el-select v-model="form.petType" placeholder="宠物类型">
              <el-option style="line-height: 38px; height: 38px;"
                v-for="item in typeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>

          </el-form-item>
          <el-form-item v-if="!isEdit" class="topMargin" label="预设模板：">
            <template >
              <el-select @change="aboutTemplateChange()" v-model="aboutTemplate" placeholder="预设模板">
                <el-option style="line-height: 38px; height: 38px;"
                  v-for="item in templateOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </template>
          </el-form-item>
          <el-form-item class="topMargin" label="地区：">
            <el-cascader
            size="large"
            :options="options"
            v-model="selectedOptions"
            @change="regionChange"
            :props="{ expandTrigger: 'hover' }">
            </el-cascader>
          </el-form-item>
          <el-form-item class="topMargin" label="健康信息：">

            <div>
              <el-switch style="margin-left: 14px; margin-bottom: 4px;"
              v-model="form.isDewormed"
              @change="form.isDewormed == !form.isDewormed"
              active-text="已驱虫"
              active-value=true
              inactive-text="未驱虫"
              inactive-value=false>
              </el-switch>

              <el-switch style="margin-left: 14px; margin-bottom: 4px;"
              v-model="form.isVaccinated"
              @change="form.isVaccinated == !form.isVaccinated"
              active-text="已打疫苗"
              active-value=true
              inactive-text="未打疫苗"
              inactive-value=false>
              </el-switch>

              
            </div>
          </el-form-item>
          <el-form-item>

            <el-switch style="margin-left: 14px; margin-bottom: 4px;"
            v-model="form.isSterilized"
            @change="form.isSterilized == !form.isSterilized"
            active-text="已绝育"
            active-value=true
            inactive-text="未绝育"
            inactive-value=false>
            </el-switch>
            <el-switch style="margin-left: 14px; margin-bottom: 4px;"
            v-model="form.gender"
            @change="form.gender == !form.gender"
            active-text="雄性"
            active-value=true
            inactive-text="雌性"
            inactive-value=false>
            </el-switch>
          </el-form-item>

        </el-col>

        <el-col :span="10">

            <div class="col-md-4 about-colorbar">
              <div class="row">
                <div id="rate-wrap" class="col-sm-11 col-md-10" style="margin-top: 30px;">
                  <h2 class="mobmid">宠物属性 </h2>

                  <div>
                    <p>kind</p>
                    <div class="progress" slot="reference" >
                      <el-rate class="rate-mark-row"
                      :max="10"
                      v-model="form.rateData.kind"></el-rate> 
                      <div :class="'progress-bar progress-bar-1'" role="progressbar" aria-valuenow="65" aria-valuemin="0" aria-valuemax="100" :style="{'width': form.rateData.kind * 10 + '%'}">
                        <span class="sr-only">{{ form.rateData.kind * 10 }}% </span>
                        
                      </div>
                      <i v-if="!form.rateData.kind" class="el-icon-edit mainright" style="line-height: 20px; margin-right: 12px;"></i>
                      <i v-if="form.rateData.kind" class="el-icon-check mainright" style="line-height: 20px; margin-right: 12px;"></i>
                    </div>
                  </div>
                  <div>
                    <p>vitality</p>
                    <div class="progress" slot="reference" >
                      <el-rate class="rate-mark-row"
                      :max="10"
                      v-model="form.rateData.vitality"></el-rate> 
                      <div :class="'progress-bar progress-bar-1'" role="progressbar" aria-valuenow="65" aria-valuemin="0" aria-valuemax="100" :style="{'width': form.rateData.vitality * 10 + '%'}">
                        <span class="sr-only">{{ form.rateData.vitality * 10 }}% </span>
                        
                      </div>
                      <i v-if="!form.rateData.vitality" class="el-icon-edit mainright" style="line-height: 20px; margin-right: 12px;"></i>
                      <i v-if="form.rateData.vitality" class="el-icon-check mainright" style="line-height: 20px; margin-right: 12px;"></i>
                    </div>
                  </div>
                  <div>
                    <p>independent</p>
                    <div class="progress" slot="reference" >
                      <el-rate class="rate-mark-row"
                      :max="10"
                      v-model="form.rateData.independent"></el-rate> 
                      <div :class="'progress-bar progress-bar-1'" role="progressbar" aria-valuenow="65" aria-valuemin="0" aria-valuemax="100" :style="{'width': form.rateData.independent * 10 + '%'}">
                        <span class="sr-only">{{ form.rateData.independent * 10 }}% </span>
                        
                      </div>
                      <i v-if="!form.rateData.independent" class="el-icon-edit mainright" style="line-height: 20px; margin-right: 12px;"></i>
                      <i v-if="form.rateData.independent" class="el-icon-check mainright" style="line-height: 20px; margin-right: 12px;"></i>
                    </div>
                  </div>
                  <div>
                    <p>iq</p>
                    <div class="progress" slot="reference" >
                      <el-rate class="rate-mark-row"
                      :max="10"
                      v-model="form.rateData.iq"></el-rate> 
                      <div :class="'progress-bar progress-bar-1'" role="progressbar" aria-valuenow="65" aria-valuemin="0" aria-valuemax="100" :style="{'width': form.rateData.iq * 10 + '%'}">
                        <span class="sr-only">{{ form.rateData.iq * 10 }}% </span>
                        
                      </div>
                      <i v-if="!form.rateData.iq" class="el-icon-edit mainright" style="line-height: 20px; margin-right: 12px;"></i>
                      <i v-if="form.rateData.iq" class="el-icon-check mainright" style="line-height: 20px; margin-right: 12px;"></i>
                    </div>
                  </div>
                  
                </div>
              </div>
            </div>

        </el-col>
      </el-row>

      
      
      <el-form-item class="topMargin" label="封面：" prop="cover">
        <el-row>
          <el-col :span="10">
            <!-- <el-input v-model="form.cover"></el-input> -->
            <el-upload
              class="avatar-uploader"
              :action=uploadCoverURL
              ref="upload"
              :headers="headers"
              :show-file-list="false"
              :auto-upload="true"
              :before-upload="beforeUpload"
              :on-success="handleAvatarSuccess"
              :on-change="handleChange"
              :on-remove="handleRemove"
            >
            <el-image v-if="form.cover" class="avatar"
              :src="form.cover"
              style="width: 368px;"
              fit="cover"></el-image>
            <!-- <img v-if="form.cover" :src="form.cover" class="avatar"> -->
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              
            </el-upload>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item class="topMargin" label="描述：" prop="description" style="width: 800px;">
        <el-input
          type="textarea"
          placeholder="宠物描述"
          v-model="form.description"
          maxlength="200"
          :rows="5"
          show-word-limit
        >
        </el-input>
      </el-form-item>
      

      <!-- WWWWWWWWWWWWWWWWW -->
      <!-- <el-form-item label="About:" prop="description">

      </el-form-item> -->
      <el-form-item class="topMargin" label="About：">
        <el-table
        :data="form.about"
        style="width: 600px">
          <el-table-column
            label="属性名称"
            align="center"

            width="200">
            <template slot-scope="scope">
              <el-form-item :prop="'about.'+scope.$index+'.name'" 
                :rules="rules.about">
                  <el-input v-model="scope.row.name"></el-input>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column
            label="属性值"
            align="center"
            width="200">
            <template slot-scope="scope">
              <el-form-item :prop="'about.'+scope.$index+'.value'" 
                :rules="rules.about">
                  <el-input v-model="scope.row.value"></el-input>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column
           label="操作"
           align="center"
           width="200">

            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
      
      <el-form-item class="topMargin">
        <el-button style="margin-left: 152px;" @click="addDomain">新增属性</el-button>

      </el-form-item>
      <!-- WWWWWWWWWWWWW -->

      <el-form-item class="topMargin" label="Content：" prop="content">
        <el-tiptap 
        :extensions="extensions" 
        v-model="form.content" 
        lang="zh" 
        placeholder="请输入宠物详细内容"
        height="610"
        ></el-tiptap>
      </el-form-item>
      <!-- 条件渲染 根据isEdit显示发布或者修改 -->
      <div style="display: flex; align-items: center;justify-content: center;">
        <el-form-item class="topMargin" v-if="!isEdit">
          <div style="margin-right: 100px;">
            <el-button type="primary" @click="onSubmit(false)">发布</el-button>
            <el-button @click="onSubmit(true)">存入草稿</el-button>
          </div>
        </el-form-item>
        <el-form-item class="topMargin" v-else>
          <div style="margin-right: 100px;">
          <el-button type="primary" @click="editArticle">保存修改</el-button>
          <el-button >取消</el-button>
          </div>
        </el-form-item>
      </div>
      
    </el-form>
  </el-card>
</template>
 
<script>
import {
  ElementTiptap,
  // necessary extensions
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  Image,
  CodeBlock,
  Blockquote,
  TextAlign,
  Indent,
  Table,
  TableHeader,
  TableCell,
  TableRow,
  TextColor,
  HorizontalRule
  // LineHeigh
} from 'element-tiptap'
import 'element-tiptap/lib/index.css'
import { regionData, codeToText } from 'element-china-area-data'
// 上传图片接口
import * as apiUpload from "@/api/upload.js";
import * as apiAdopt from "@/api/adopt.js";
import storage from '@/plugins/storage';
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'AdoptWorkspace',
  components: {
    'el-tiptap': ElementTiptap
  },
  
  mounted() {
    this.headers = {'accessToken': storage.getItem('accessToken')};
    this.uploadCoverURL = apiUpload.uploadAdoptCoverURL();
    this.initData()
  },
  data () {
    return {
      uploadCoverURL: '',
      headers: {},
      file: null,
      form: {
        adoptId: null,
        title: '',
        description: '',
        about: [],
        content: '',
        cover: '',      // 封面图片
        petType: '', // adopt类型
        contentType: 'HTML',
        isDewormed: false, //是否驱虫
        isVaccinated: false, //是否打疫苗
        isSterilized: false, // 是否绝育
        gender: false, //宠物性别 true 雄 false 雌

        region: '', //地址
        rateData: {
          kind: 0,
          vitality: 0,
          independent: 0,
          iq: 0,
        },
      },
      aboutTemplate: '', // about模板
      contentTemplate: '', // content模板
      templateOptions: [{"name":"cat","label":"猫咪百科"},{"name":"dog","label":"狗狗百科"},{"name":"bird","label":"鸟类百科"},{"name":"fish","label":"鱼类百科"},{"name":"turtle","label":"龟类百科"},{"name":"rabbit","label":"兔类百科"}],
      typeOptions: [{"name":"cat","label":"猫咪百科"},{"name":"dog","label":"狗狗百科"},{"name":"bird","label":"鸟类百科"},{"name":"fish","label":"鱼类百科"},{"name":"turtle","label":"龟类百科"},{"name":"rabbit","label":"兔类百科"}],

      // 地区级联选择
      options: regionData,
      selectedOptions: [],

      

      
        
      // 是否是修改 有id就是修改 没有就不是
      isEdit: false,
      // 验证规则
      rules: {
        title: [
          { required: true, message: '请输入标题', trigger: 'blur' },
          { min: 2, max: 30, message: '长度在 2 到 30 个字符', trigger: 'blur' }
        ],
        pet_Type: [{ required: true, message: '请选择宠物类型', trigger: 'change' }],
        cover: [{ required: true, message: '请上传封面', trigger: 'blur' }],
        description: [{ required: true, message: '请输入内描述容', trigger: 'blur' }],
        about: [{ required: true, message: '属性值不能为空', trigger: 'blur' }],
        content: [{ required: true, message: '请输入内容', trigger: 'blur' }],
        
      },
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }), // render command-button in bubble menu.
        new Underline({ bubble: true, menubar: false }), // render command-button in bubble menu but not in menubar.
        new Italic(),
        new Strike(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new CodeBlock(),
        new Image({
          // 默认会把图片生成 base64 字符串和内容存储在一起，如果需要自定义图片上传
          uploadRequest (file) {
            // 如果接口要求 Content-Type 是 multipart/form-data，则请求体必须使用 FormData
            const formdata = new FormData()
            //image是后台接口需要参数名
            formdata.append('file', file)
            //调用接口进行上传,拿到服务器返回的图片地址
            return apiUpload.uploadAdoptContentImage(formdata).then(res => {
              // 这个 return 是返回请求成功后拿到的url，赋值给富文本编辑器里面的img中的src属性
              return res.result.fileUrl;
            })
          } // 图片的上传方法，返回一个 Promise<url>
        }),
        new Blockquote(),
        new TextAlign(),
        new Indent(),
        new Table(),
        new TableHeader(),
        new TableCell(),
        new TableRow(),
        new HorizontalRule(),
        new TextColor()
      ]
    }
  },
  methods: {
    initData() {
      if (this.$route.params.adoptId != undefined) {
        // 修改adopt 查询adopt信息
        this.form.adoptId = this.$route.params.adoptId;
        this.isEdit = true;
        apiAdopt.getAdoptData(this.form.adoptId).then(res=>{
          if(res.success){
            this.form = res.result;
            delete this.form.creator;
            delete this.form.form.rateData;
            delete this.form.adoptState;
            delete this.form.createTime;
            delete this.form.collectionCount;
            this.form.about = JSON.parse(res.result.about);
            this.form.adoptId = this.$route.params.adoptId;
          }else{
            this.$message.error(res.message);
          }
        })
        
      }
      // 获取宠物类型选项
      apiAdopt.getAdoptPetsTypeOptions().then((res)=>{
        if(res.success){
          this.typeOptions = JSON.parse(res.result);
        }else{
          this.$message.error(res.message);
        }
      })
      // 获取模板选项
      apiAdopt.getAdoptTemplateOptions().then((res)=>{
        if(res.success){
          this.templateOptions = JSON.parse(res.result);
        }else{
          this.$message.error(res.message);
        }
      })
    },
    regionChange () {
      this.form.region = '';
      for (let i = 0; i < this.selectedOptions.length; i++) {
        this.form.region += codeToText[this.selectedOptions[i]];
          if (i < this.selectedOptions.length - 1) this.form.region += ','
      }
    },
    aboutTemplateChange() {
      if(this.aboutTemplate == ''){
        return
      }else {
        // 根据选项获取模板
        apiAdopt.getAdoptTemplate(this.aboutTemplate).then((res)=>{
          if(res.success){
            this.$message.success('获取模板成功')
            this.form.about = JSON.parse(res.result.aboutTemplate);
            this.form.content = res.result.contentTemplate;
          }else{
            this.$message.error(res.message);
          }
        })
      }
      
    },
    handleDelete(index, row) {
      this.form.about.splice(index, 1)
    },
    addDomain() {
      this.form.about.push({
        name: '',
        value: '',
      })
    },
    beforeUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
    },
    handleRemove(file, fileList) {
      this.file = null;
      console.info(this.file);
    },
    handleChange(file, fileList){
      console.info(file);
      this.file = file;
      // let URL = window.URL || window.webkitURL;
      // this.form.cover = URL.createObjectURL(file.raw);
    },
    handleAvatarSuccess(res, file) {
        console.info(file);
        this.file = file;
        let URL = window.URL || window.webkitURL;
        this.form.cover = res.result.fileUrl;
        this.$message.success('图片上传成功');
    },


    // 发布按钮
    onSubmit (draft) {
      this.$refs.ruleForm.validate(async valid => {
        if (!valid){
          this.$message.error('请完善宠物信息')
          return false
        } 
        apiAdopt.publishAdopt(this.form).then(res=>{
          if(res.success){
            this.$message.success('宠物领养信息发布成功');
            // 跳转到我的宠物领养信息页面
          }else{
            this.$message.error(res.message);
          }
        })
      })
    },
    // 修改按钮
    editArticle () {
      this.$refs.ruleForm.validate(async valid => {
        if (!valid){
          this.$message.error('请完善百科信息')
          return false
        }
        
        apiAdopt.editAdopt(this.form).then(res=>{
          if(res.success){
            this.$message.success('百科保存成功');
            // 跳转到我的百科页面
          }else{
            this.$message.error(res.message);
          }
        })
      })
    },
    // 获取频道信息
    async getChannelsFn () {
      this.loading = true
      // const data = await getChannels().catch(err => err)
      // if (data.status !== 200) return this.$message.error('频道信息获取失败')
      // this.channels = data.data.data.channels
      this.loading = false
    },
    // 查询文章
    async inquireArticleFn () {
      // const data = await inquireArticle(this.isEdit).catch(err => err)
      // if (data.status !== 200) return this.$message.error('文章信息获取失败')
      // this.form = data.data.data
    }
  },
  created () {
    // 获取频道信息
    this.getChannelsFn()
 
    // 如果是修改页面就要获取文章数据
    if (this.isEdit) {
      this.inquireArticleFn()
    }
  }
}
</script>
 
<style lang="scss">
.topMargin {
  margin-top: 30px;
}
.box-card {
  // padding-right: 20%;
  // padding-left: 10%;
  margin-left: 10%;
  margin-right: 15%;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 368px;
  height: 198px;
}
.avatar-uploader .el-upload:hover {
  border-color: #EDC79B;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 198px;
  text-align: center;
}
.avatar {
  width: 368px;
  height: 198px;
  display: block;
}

// color bar
.about-colorbar {
  width: 70%;
  float: right;
  margin-right: 10%;
}

.about-colorbar .mainleft {
    float: left;
}
.about-colorbar .mainright {
    float: right;
}
.about-colorbar .mobmid {
    margin-bottom: 10px;
}
.about-colorbar p {
    margin: 0 0 4px;
}
.about-colorbar .progress {
    height: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: #f5f5f5;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 2px rgba(0,0,0,.1);
    box-shadow: inset 0 1px 2px rgba(0,0,0,.1);
    cursor: pointer;
}
.about-colorbar .progress-bar {
    float: left;
    width: 55%;
    height: 100%;
    font-size: 12px;
    line-height: 20px;
    color: #fff;
    text-align: center;
    -webkit-box-shadow: inset 0 -1px 0 rgba(0,0,0,.15);
    box-shadow: inset 0 -1px 0 rgba(0,0,0,.15);
    -webkit-transition: width .6s ease;
    -o-transition: width .6s ease;
    transition: width .6s ease;
    
}
.about-colorbar .progress-bar-0 {
    background-color: #5cb85c;
}
.about-colorbar .progress-bar-1 {
    background-color: #5bc0de;
}
.about-colorbar .progress-bar-2 {
    background-color: #f0ad4e;
}
.about-colorbar .progress-bar-3 {
    background-color: #d9534f;
}

.about-colorbar img {
    max-width: 100%
}

.about-colorbar .propic {
    border-radius: 10px;
}

.about-colorbar hr {
    margin: 40px 0;
}
.about-colorbar p,
.about-colorbar a {
    line-height: 1.7em;
